import { Component, Input } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { APP_ROUTE, PREMIUM_UNLIMITED_OPTIONS } from '@core/constants';
import { SnackService } from '@core/services/snack.service';
import { ButtonType } from '@shared/components/button/button.component';
import { SubscriptionOfferDialogComponent } from '../subscription-offer-dialog/subscription-offer-dialog.component';
import { ChangeCardDilaogComponent } from './change-card-dilaog/change-card-dilaog.component';
import { SubscriptionInfo } from '@core/models';
import { SubscriptionStatus, SubscriptionType } from '@core/enums/profile.enum';
import { SubscriptionTier } from '@core/enums/profile.enum';
import { MyProfileFacade } from '@feature/my-profile/my-profile.facade';
import { environment } from '@environments/environment';
import { finalize } from 'rxjs';
import { WindowService } from '@core/services/window.service';

@Component({
    selector: 'findqo-subscription',
    templateUrl: './subscription.component.html',
    styleUrls: ['./subscription.component.scss'],
    standalone: false
})
export class SubscriptionComponent {
	constructor(
		public snackBarService: SnackService,
		public dialog: MatDialog,
		private readonly profileFacade: MyProfileFacade,
		private readonly windowService: WindowService
	) {}

	@Input() subscriptionInfo: SubscriptionInfo;
	@Input() isAgentUser;

	readonly subscriptionTier = SubscriptionTier;
	readonly appRoute = APP_ROUTE;
	readonly buttonType = ButtonType;
	readonly subscriptionType = SubscriptionType;
	readonly subscriptionStatus = SubscriptionStatus;
	readonly paymentOptions = PREMIUM_UNLIMITED_OPTIONS;

	isLoading = false;
	selectedPaymentPlan = 'monthly';

	readonly themeList = [
		{ border: '4', width: '100%', margin: '0', height: '40px' },
		{ border: '4', width: '100%', margin: '0', height: '20px' },
		{ border: '4', width: '100%', margin: '0', height: '20px' },
	];

	openSubscription(): void {
		this.dialog
			.open(SubscriptionOfferDialogComponent, {
				disableClose: true,
				width: '500px',
				height: '600px',
			})
			.afterClosed();
	}

	changeCard(): void {
		this.dialog.open(ChangeCardDilaogComponent, {
			disableClose: true,
			maxWidth: '500px',
			backdropClass: 'inquiry-backdrop',
		});
	}

	handlePlanChange(plan: string) {
		this.selectedPaymentPlan = plan;
	}

	protected generatePaymentLink(): void {
		const planTierId = this.paymentOptions[this.selectedPaymentPlan].planTierId;

		this.isLoading = true;
		this.profileFacade
			.generatePaymentLink({
				planTierId,
				successUrl: `${environment.appUrl}/${APP_ROUTE.agentHub.dashboard}/${APP_ROUTE.businessProfile.paymentSuccess}`,
				cancelUrl: `${environment.appUrl}/${APP_ROUTE.agentHub.businessAccount}`,
			})
			.pipe(finalize(() => (this.isLoading = false)))
			.subscribe((res) => this.windowService.reference.location.replace(res.url));
	}
}
